@import '../../App.scss';

footer {
    height: 50vh;
    padding: 2rem 3rem;
    background: $theme-color;
}

footer .footer__socialNetworks {
    display: flex;
}

footer .icon_holder {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
}

footer .icon_holder a > svg {
    width: 100%;
}

footer .footer__info {
    position: absolute;
    bottom: 0;
    padding: 2rem 0;
    height: 10vh;
}

footer .footer__logo {
    height: 100%;
}