$black: #000;
$white: #fff;

//color-varients
$theme-color: #FFC20E;
$red: #FF0E0E;
$amber: #FFC20E;
$green: #31FF0E;
$blue: #0EFFFA;
$violet: #5C0EFF;
$pink: #FF0E80;


$vertical-margin: 2rem;
$horizontal-margin: 3rem;

.container {
  position: relative;
}

.btn {
  background-color: $black;
  /* display: inline-block; */
  border: solid 2px transparent;
  outline: none;
  padding: 0.5rem;
  /* font settings */
  color: $white;
  font-family: 'Inter', 'source-han-sans-simplified-c', sans-serif; 
  font-weight: 900;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}

.btn:hover {
  border: 2px solid $black;
  background-color: transparent;
  color: $black;
;
}

.text_btn__chn {
    position: relative;
    top: -0.2rem;
}